  font-size: 13px;
  padding-bottom: 10px;
}

.sidebar_recentItem:hover {
  background-color: whitesmoke;
  border-radius: 10px;
  cursor: pointer;
  color: black;
}

.sidebar_hash {
  margin-right: 10px;
  margin-left: 5px;
}

.widgets {
  position: -webkit-sticky;
  position: sticky;
  top: 80px;
  flex: 0.2 1;
  background-color: white;
  border-radius: 10px;
  border: 1px solid lightgray;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding-bottom: 10px;
}

.widgets_header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
}

.widgets_header > h2 {
  font-size: 16px;
  font-weight: 400;
}

.widgets_article {
  display: flex;
  padding: 10px;
  cursor: pointer;
}

.widgets_article:hover {
  background-color: whitesmoke;
}

.widgets_articleLeft {
  color: #0177b7;
  margin-right: 5px;
}

.widgets_articleLeft > .MuiSvgIcon-root {
  font-size: 15px;
}

.widgets_articleRight {
  flex: 1 1;
}

.widgets_articleRight > h4 {
  font-size: 14px;
}

.widgets_articleRight > p {
  font-size: 12px;
  color: gray;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-y: scroll;
  display: block;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

